import React, { useState } from 'react'
import styles from './index.module.scss'
import logo from '@/styles/imgs/logo.png'
import Model from '@/pages/Model'
import cancel_3 from '@/styles/imgs/cancel_3.png'
import useMetaMask from '@/lib/onBoarding/useMetaMask'
function Header (props) {

  const [contents, setContents] = useState(0)
  const onContent = (index) => {
    props.EditActive(index)
    setContents(0)
  }
  const {OnBoardingButton} = useMetaMask()

  return (
    <div className={styles.headers}>
      <div className='content'>
        <div className='right'>
           <img src={logo} className='logo' alt=''></img>
          </div>
        <div className='left'>
          {
            contents == 0 ?
              <>
                 {/*<button className='left_connect'>Connect Wallet</button>*/}
                  <OnBoardingButton className='left_connect'/>
               <div className='left_path' onClick={() => setContents(1)}>
                 <span />
                 <span />
                 <span />
              </div>
              </>
              :<div className='cancel'><img src={cancel_3} className='cancel_2' onClick={()=>setContents(0)}></img></div>
          }
          
          </div>
      </div>
      {
         contents == 0 ? '':<Model onContent={onContent} actives={props.actives}/>
      }
      
    </div>
  )
}

export default Header