import logo from './logo.svg';
import './App.css';
import Header from './pages/Header';
import Lending from './pages/Lending';
import Sonne from './pages/Sonne';
import Market from './pages/Market';
import { withRouter, Switch, Route } from 'react-router-dom';
import Stake from './pages/Stake';
import Claim from './pages/Claim';
import Fundraising from './pages/Fundraising';
import { useState } from 'react';
import history from '@/utils/history'
function App () {
  const [actives, setActive] = useState(0)
  const EditActive = (value) => {
    setActive(value)
  }
  return (

       <div className="App">
        <Header EditActive={EditActive} actives={actives}/>
        
      {
        actives==0? <Lending />: actives==1?<Market />: actives==2?<Stake />:actives==3? <Claim />:actives==4?  <Fundraising /> : <Lending />
       }
      
     
     
     
      <Sonne />
    </div>
      
    
  );
}

export default App;
