import React from "react";
import {ethers} from "ethers";
import MetaMaskOnBoarding from "./metamask-onboarding.es";

const ONBOARD_TEXT = 'install Wallet';
const CONNECT_TEXT = 'Connect';
// const ONBOARD_TEXT = '';
// const CONNECT_TEXT = '';

export default function useMetamask() {
    const [buttonText, setButtonText] = React.useState(ONBOARD_TEXT);
    const [isDisabled, setDisabled] = React.useState(false);
    const [chainId, setChainId] = React.useState(0);
    const [account, setAccount] = React.useState([]);
    const [signer, setSigner] = React.useState([]);
    const onBoarding = React.useRef();


    const setAccounts = (accounts) => {
        setAccount(accounts[0])
        let p = new ethers.providers.Web3Provider(window.ethereum)
        setSigner(p.getSigner())
    }

    const shortAddress = () => {
        return account.slice(0, 4) + "..." + account.slice(-4)
    }

    React.useEffect(() => {
        if (!onBoarding.current) {
            onBoarding.current = new MetaMaskOnBoarding();
        }
        window.ethers = ethers
    }, []);
    React.useEffect(() => {
        if (signer && signer.provider && signer.provider.provider) {
            setChainId(parseInt(signer.provider.provider.chainId))
        }
    }, [signer]);

    React.useEffect(() => {
        if (MetaMaskOnBoarding.isMetaMaskInstalled()) {
            if (account.length > 0) {
                setButtonText(shortAddress());
                setDisabled(true);
                onBoarding.current.stopOnboarding();
            } else {
                setButtonText(CONNECT_TEXT);
                setDisabled(false);
            }
        }
    }, [account]);

    React.useEffect(() => {
        function handleNewAccounts(newAccounts) {
            setAccounts(newAccounts);
        }
        if (MetaMaskOnBoarding.isMetaMaskInstalled()) {
            window.ethereum
                .request({ method: 'eth_requestAccounts' })
                .then(handleNewAccounts);
            window.ethereum.on('accountsChanged', handleNewAccounts);
            return () => {
                console.log("window.ethereum.off: ", window.ethereum.off)
                window.ethereum.off && window.ethereum.off('accountsChanged', handleNewAccounts);
            };
        }
    }, []);

    const onClick = () => {
        if (MetaMaskOnBoarding.isMetaMaskInstalled()) {
            window.ethereum
                .request({ method: 'eth_requestAccounts' })
                .then((newAccounts) => setAccounts(newAccounts));
        } else {
            onBoarding.current.startOnboarding();
        }
    };

    const OnBoardingButton = ({className}) => {
        return <button className={className} disabled={isDisabled} onClick={onClick}>
            {/*<span className="text-primary">*/}
            {/*    <svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" fill="currentColor"*/}
            {/*         className="bi bi-link-45deg" viewBox="0 0 16 16">*/}
            {/*        <path*/}
            {/*            d="M4.715 6.542 3.343 7.914a3 3 0 1 0 4.243 4.243l1.828-1.829A3 3 0 0 0 8.586 5.5L8 6.086a1.002 1.002 0 0 0-.154.199 2 2 0 0 1 .861 3.337L6.88 11.45a2 2 0 1 1-2.83-2.83l.793-.792a4.018 4.018 0 0 1-.128-1.287z"/>*/}
            {/*        <path*/}
            {/*            d="M6.586 4.672A3 3 0 0 0 7.414 9.5l.775-.776a2 2 0 0 1-.896-3.346L9.12 3.55a2 2 0 1 1 2.83 2.83l-.793.792c.112.42.155.855.128 1.287l1.372-1.372a3 3 0 1 0-4.243-4.243L6.586 4.672z"/>*/}
            {/*    </svg>*/}
            {/*</span>*/}

            {buttonText}
        </button>
    }
    return {
        chainId,
        account,
        signer,
        OnBoardingButton
    }
}